import React, { useState } from "react";
import axios from "axios";
import {
    Box,
    Typography,
    useTheme,
    useMediaQuery,
    TextField,
    Button,
    Alert,
    Collapse,
    Card
} from "@mui/material";
import { Link } from "react-router-dom";
const handleResizeObserverError = () => {
    const resizeObserverErrDiv = document.createElement('div');
    resizeObserverErrDiv.id = 'webpack-dev-server-client-overlay-div';
    resizeObserverErrDiv.style.display = 'none';
    document.body.appendChild(resizeObserverErrDiv);
};
handleResizeObserverError();

const GenerateParagraph = () => {
    const theme = useTheme();
    const isNotMobile = useMediaQuery("(min-width: 1000px)");

    // States
    const [words, setWords] = useState("");
    const [paragraph, setParagraph] = useState("");
    const [error, setError] = useState("");

    // Handle form submission
    const handleSubmit = async (e) => {
        e.preventDefault();
        const wordArray = words.split(',').map(word => word.trim());

        try {
            const { data } = await
                axios.post(`${process.env.REACT_APP_API}/api/v1/open/generate-paragraph`,
                    { words: wordArray });
            setParagraph(data.paragraph);
            setError("");
        } catch (err) {
            if (err.response?.data?.message) {
                setError(err.response.data.message);
            } else if (err.message) {
                setError(err.message);
            }
            setTimeout(() => {
                setError("");
            }, 5000);
        }
    };

    return (
        <Box
            width={isNotMobile ? "40%" : "80%"}
            p={"2rem"}
            m={"2rem auto"}
            borderRadius={5}
            sx={{ boxShadow: 5 }}
            backgroundColor={theme.palette.background.alt}
        >
            <Collapse in={!!error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>
            <form onSubmit={handleSubmit}>
                <Typography variant="h3">Generate Paragraph</Typography>

                <TextField
                    placeholder="Enter words separated by commas"
                    type="text"
                    multiline
                    required
                    margin="normal"
                    fullWidth
                    value={words}
                    onChange={(e) => setWords(e.target.value)}
                />

                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    size="large"
                    sx={{ color: "white", mt: 2 }}
                >
                    Generate
                </Button>
                <Typography mt={2}>
                    Not this tool? <Link to="/">Go Back</Link>
                </Typography>
            </form>

            {paragraph ? (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                        p: 2,
                    }}
                >
                    <Typography>
                        {paragraph}
                    </Typography>
                </Card>
            ) : (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                        p: 2,
                    }}
                >
                    <Typography
                        variant="h5"
                        color="natural.main"
                        sx={{
                            textAlign: "center",
                            verticalAlign: "middle",
                            lineHeight: "150px",
                        }}
                    >
                        Generated paragraph will appear here
                    </Typography>
                </Card>
            )}
        </Box>
    );
};

export default GenerateParagraph;
