import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import axios from "axios";
import {
    Box,
    Typography,
    useTheme,
    useMediaQuery,
    TextField,
    Button,
    Alert,
    Collapse,
    Card,
} from "@mui/material";
const handleResizeObserverError = () => {
    const resizeObserverErrDiv = document.createElement('div');
    resizeObserverErrDiv.id = 'webpack-dev-server-client-overlay-div';
    resizeObserverErrDiv.style.display = 'none';
    document.body.appendChild(resizeObserverErrDiv);
};
handleResizeObserverError();

const ChatBot = () => {
    const theme = useTheme();
    const navigate = useNavigate();
    // Media query to determine if the screen is wide enough to be considered not mobile
    const isNotMobile = useMediaQuery("(min-width: 1000px)");

    // State variables to manage the input text, the response from the chatbot, and any errors
    const [text, setText] = useState("");
    const [response, setResponse] = useState("");
    const [error, setError] = useState("");

    // Function to handle form submission
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data } = await
                axios.post(`${process.env.REACT_APP_API}/api/v1/open/chat`,
                    { message: text });
            console.log(data);
            setResponse(data.reply);  // Update this to match your backend's expected response structure
            setError(""); // Clear any previous errors
        } catch (err) {
            console.log(err);
            if (err.response?.data?.message) {
                setError(err.response.data.message);
            } else if (err.message) {
                setError(err.message);
            }
            setTimeout(() => {
                setError("");
            }, 5000);
        }
    };

    return (
        <Box
            width={isNotMobile ? "40%" : "80%"}
            p={"2rem"}
            m={"2rem auto"}
            borderRadius={5}
            sx={{ boxShadow: 5 }}
            backgroundColor={theme.palette.background.alt}
        >
            {/* Display error if any */}
            <Collapse in={!!error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>

            {/* Form for user input */}
            <form onSubmit={handleSubmit}>
                <Typography variant="h3">Ask with Chatbot</Typography>

                <TextField
                    placeholder="Add your text"
                    type="text"
                    multiline
                    required
                    margin="normal"
                    fullWidth
                    value={text}
                    onChange={(e) => setText(e.target.value)}
                />

                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    size="large"
                    sx={{ color: "white", mt: 2 }}
                >
                    Chat
                </Button>
                <Typography mt={2}>
                    Not this tool? <Link to="/">GO BACK</Link>
                </Typography>
            </form>

            {/* Display chatbot response or placeholder */}
            <Card
                sx={{
                    mt: 4,
                    border: 1,
                    boxShadow: 0,
                    height: "500px",
                    borderRadius: 5,
                    borderColor: "natural.medium",
                    bgcolor: "background.default",
                }}
            >
                {response ? (
                    <Typography p={2}>{response}</Typography>
                ) : (
                    <Typography
                        variant="h5"
                        color="natural.main"
                        sx={{
                            textAlign: "center",
                            verticalAlign: "middle",
                            lineHeight: "450px",
                        }}
                    >
                        Bot Response
                    </Typography>
                )}
            </Card>
        </Box>
    );
};

export default ChatBot;
