import React, { useEffect, useState } from "react";
import { Box, Typography, Card, Stack } from "@mui/material";
import { useNavigate } from "react-router-dom";
import DescriptionRounded from "@mui/icons-material/DescriptionRounded";
import FormatAlignLeftOutlined from "@mui/icons-material/FormatAlignLeftOutlined";
import ChatRounded from "@mui/icons-material/ChatRounded";

const Homepage = () => {
    const navigate = useNavigate();
    const [isAuthenticated, setIsAuthenticated] = useState(false);

    useEffect(() => {
        // Check if user is logged in
        const loggedIn = localStorage.getItem("isAuthenticated") === "true";
        setIsAuthenticated(loggedIn);
    }, []);

    const handleNavigation = (path) => {
        if (isAuthenticated) {
            navigate(path);
        } else {
            navigate("/register");
        }
    };

    return (
        <Box
            sx={{
                display: "flex",
                flexDirection: { xs: "column", sm: "row" },
                flexWrap: "wrap",
                justifyContent: "center",
                gap: 2, // space between cards
            }}
        >
            <Box p={2} flexBasis={{ xs: "100%", sm: "auto" }}>
                <Typography variant="h4" mb={2} fontWeight="bold">
                    Text Generation
                </Typography>
                <Card
                    onClick={() => handleNavigation("/summary")}
                    sx={{
                        boxShadow: 2,
                        borderRadius: 5,
                        height: 190,
                        width: 200,
                        "&:hover": {
                            border: 2,
                            boxShadow: 0,
                            borderColor: "primary.dark",
                            cursor: "pointer",
                        },
                    }}
                >
                    <DescriptionRounded
                        sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                    />
                    <Stack p={3} pt={0}>
                        <Typography fontWeight="bold" variant="h5">
                            TEXT SUMMARY
                        </Typography>
                        <Typography variant="h6">
                            Summarize long text into short sentences
                        </Typography>
                    </Stack>
                </Card>
            </Box>

            <Box p={2} flexBasis={{ xs: "100%", sm: "auto" }}>
                <Typography variant="h4" mb={2} fontWeight="bold">
                    Paragraph Generation
                </Typography>
                <Card
                    onClick={() => handleNavigation("/paragraph")}
                    sx={{
                        boxShadow: 2,
                        borderRadius: 5,
                        height: 190,
                        width: 200,
                        "&:hover": {
                            border: 2,
                            boxShadow: 0,
                            borderColor: "primary.dark",
                            cursor: "pointer",
                        },
                    }}
                >
                    <FormatAlignLeftOutlined
                        sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                    />
                    <Stack p={3} pt={0}>
                        <Typography fontWeight="bold" variant="h5">
                            PARAGRAPH
                        </Typography>
                        <Typography variant="h6">
                            Generate Paragraph with words
                        </Typography>
                    </Stack>
                </Card>
            </Box>

            <Box p={2} flexBasis={{ xs: "100%", sm: "auto" }}>
                <Typography variant="h4" mb={2} fontWeight="bold">
                    AI ChatBot
                </Typography>
                <Card
                    onClick={() => handleNavigation("/chatbot")}
                    sx={{
                        boxShadow: 2,
                        borderRadius: 5,
                        height: 190,
                        width: 200,
                        "&:hover": {
                            border: 2,
                            boxShadow: 0,
                            borderColor: "primary.dark",
                            cursor: "pointer",
                        },
                    }}
                >
                    <ChatRounded
                        sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                    />
                    <Stack p={3} pt={0}>
                        <Typography fontWeight="bold" variant="h5">
                            Chatbot
                        </Typography>
                        <Typography variant="h6">Chat With AI Chatbot</Typography>
                    </Stack>
                </Card>
            </Box>

            <Box p={2} flexBasis={{ xs: "100%", sm: "auto" }}>
                <Typography variant="h4" mb={2} fontWeight="bold">
                    JavaScript Converter
                </Typography>
                <Card
                    onClick={() => handleNavigation("/js-converter")}
                    sx={{
                        boxShadow: 2,
                        borderRadius: 5,
                        height: 190,
                        width: 200,
                        "&:hover": {
                            border: 2,
                            boxShadow: 0,
                            borderColor: "primary.dark",
                            cursor: "pointer",
                        },
                    }}
                >
                    <ChatRounded
                        sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                    />
                    <Stack p={3} pt={0}>
                        <Typography fontWeight="bold" variant="h5">
                            JS CONVERTER
                        </Typography>
                        <Typography variant="h6">
                            Translate English to JavaScript code
                        </Typography>
                    </Stack>
                </Card>
            </Box>

            <Box p={2} flexBasis={{ xs: "100%", sm: "auto" }}>
                <Typography variant="h4" mb={2} fontWeight="bold">
                    AI Sci-Fi Images
                </Typography>
                <Card
                    onClick={() => handleNavigation("/scifi-image")}
                    sx={{
                        boxShadow: 2,
                        borderRadius: 5,
                        height: 190,
                        width: 200,
                        "&:hover": {
                            border: 2,
                            boxShadow: 0,
                            borderColor: "primary.dark",
                            cursor: "pointer",
                        },
                    }}
                >
                    <ChatRounded
                        sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                    />
                    <Stack p={3} pt={0}>
                        <Typography fontWeight="bold" variant="h5">
                            Sci-Fi Image
                        </Typography>
                        <Typography variant="h6">Generate Sci-Fi images</Typography>
                    </Stack>
                </Card>
            </Box>
        </Box>
    );
};

export default Homepage;
