// export default Summary;
import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {
    Box,
    Typography,
    useTheme,
    useMediaQuery,
    TextField,
    Button,
    Alert,
    Collapse,
    Card,
} from "@mui/material";
const handleResizeObserverError = () => {
    const resizeObserverErrDiv = document.createElement('div');
    resizeObserverErrDiv.id = 'webpack-dev-server-client-overlay-div';
    resizeObserverErrDiv.style.display = 'none';
    document.body.appendChild(resizeObserverErrDiv);
};
handleResizeObserverError();
const Summary = () => {
    const theme = useTheme();
    // Media query for responsiveness
    const isNotMobile = useMediaQuery("(min-width: 1000px)");

    // States
    const [text, setText] = useState("");
    const [summary, setSummary] = useState("");
    const [error, setError] = useState("");

    // Handle form submission
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post(`${process.env.REACT_APP_API}/api/v1/open/summarize-text`, { text });
            const data = response.data;

            // Check if summary exists and update state
            if (data.summary) {
                setSummary(data.summary);
                setError("");
            } else {
                setSummary("No summary available");
            }

        } catch (err) {
            console.error(err);

            // Update error state with message if available
            if (err.response?.data?.message) {
                setError(err.response.data.message);
            } else if (err.message) {
                setError(err.message);
            }

            setTimeout(() => {
                setError("");
            }, 5000);
        }
    };

    return (
        <Box
            width={isNotMobile ? "40%" : "80%"}
            p={"2rem"}
            m={"2rem auto"}
            borderRadius={5}
            sx={{ boxShadow: 5 }}
            backgroundColor={theme.palette.background.alt}
        >
            <Collapse in={!!error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>
            <form onSubmit={handleSubmit}>
                <Typography variant="h3">Summarize Text</Typography>

                <TextField
                    placeholder="Add your text"
                    type="text"
                    multiline
                    required
                    margin="normal"
                    fullWidth
                    value={text}
                    onChange={(e) => setText(e.target.value)}
                />

                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    size="large"
                    sx={{ color: "white", mt: 2 }}
                >
                    Submit
                </Button>
                <Typography mt={2}>
                    Not this tool? <Link to="/">Go Back</Link>
                </Typography>
            </form>

            {summary ? (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        height: "500px",
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                    }}
                >
                    <Typography p={2}>{summary}</Typography>
                </Card>
            ) : (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        height: "500px",
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                    }}
                >
                    <Typography
                        variant="h5"
                        color="natural.main"
                        sx={{
                            textAlign: "center",
                            verticalAlign: "middle",
                            lineHeight: "450px",
                        }}
                    >
                        Summary Will Appear Here
                    </Typography>
                </Card>
            )}
        </Box>
    );
};

export default Summary;
